.showInfo {
    display: flexbox;
    flex-direction: column;
}
.showInfo > div {
    margin-top: 1rem;
}
.showQuestion p{
    border-bottom: 0.05rem solid rgb(0, 0, 0);
}
.quizNavButtons{
    margin-top: 1rem;
}
.percent {
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none; 
}
.answerOption label{
    padding: 0.5rem 0;
}
.answerOption label:hover{
    background-color: rgb(240, 240, 240);
    border-radius: .5rem;
    margin: 0;
}
.ShowResults{
    text-align: center;
}